// src/components/exercise-common/InstructionBox/index.js
import { useState, useCallback } from "react";
import { View, Text, TouchableOpacity, Animated } from "react-native";
import PropTypes from "prop-types";
import { Ionicons } from "@expo/vector-icons";
import styles from "./style";

/**
 * Boîte d'instructions pour les exercices avec possibilité
 * de les minimiser/maximiser
 * 
 * @param {string} title - Titre de la boîte d'instructions
 * @param {string} instructions - Texte principal des instructions
 * @param {Array<string>} examples - Liste d'exemples à afficher
 * @param {Array<string>} tips - Liste d'astuces à afficher
 * @param {boolean} initiallyExpanded - État initial (ouvert/fermé)
 * @param {string} variant - Variante de style ('standard', 'compact', 'highlighted')
 * @param {string} primaryColor - Couleur principale pour les icônes et bordures
 */
const InstructionBox = ({
  title = "Instructions",
  instructions,
  examples = [],
  tips = [],
  initiallyExpanded = true,
  variant = "standard", // 'standard', 'compact', 'highlighted'
  primaryColor = "#5E60CE",
}) => {
  // États
  const [expanded, setExpanded] = useState(initiallyExpanded);

  // Animation pour la hauteur (expansion/contraction)
  const [animation] = useState(new Animated.Value(initiallyExpanded ? 1 : 0));

  // Toggle l'expansion
  const toggleExpanded = useCallback(() => {
    const toValue = expanded ? 0 : 1;

    Animated.timing(animation, {
      toValue,
      duration: 300,
      useNativeDriver: false,
    }).start();

    setExpanded((prev) => !prev);
  }, [expanded, animation]);

  // Déterminer le style en fonction de la variante
  const getVariantStyle = () => {
    switch (variant) {
      case "compact":
        return [styles.compactContainer];
      case "highlighted":
        return [styles.highlightedContainer, { borderColor: primaryColor }];
      case "standard":
      default:
        return [styles.standardContainer];
    }
  };

  // Animation pour la hauteur maximale
  const maxHeight = animation.interpolate({
    inputRange: [0, 1],
    outputRange: [60, 1000], // Valeurs approximatives, s'ajustera automatiquement
  });

  // Animation pour la rotation de l'icône
  const rotateIcon = animation.interpolate({
    inputRange: [0, 1],
    outputRange: ["0deg", "180deg"],
  });

  return (
    <Animated.View style={[styles.container, getVariantStyle(), { maxHeight }]}>
      <TouchableOpacity
        style={styles.headerContainer}
        onPress={toggleExpanded}
        activeOpacity={0.7}
      >
        <View style={styles.titleContainer}>
          <Ionicons
            name="information-circle"
            size={24}
            color={primaryColor}
            style={styles.titleIcon}
          />
          <Text style={[styles.title, { color: primaryColor }]}>{title}</Text>
        </View>

        <Animated.View style={{ transform: [{ rotate: rotateIcon }] }}>
          <Ionicons name="chevron-down" size={24} color="#6B7280" />
        </Animated.View>
      </TouchableOpacity>

      {expanded && (
        <View style={styles.contentContainer}>
          {instructions && (
            <Text style={styles.instructions}>{instructions}</Text>
          )}

          {examples.length > 0 && (
            <View style={styles.examplesContainer}>
              <Text style={styles.sectionTitle}>Exemples:</Text>
              {examples.map((example) => (
                <View key={example} style={styles.exampleItem}>
                  <Text style={styles.exampleText}>{example}</Text>
                </View>
              ))}
            </View>
          )}

          {tips.length > 0 && (
            <View style={styles.tipsContainer}>
              <Text style={styles.sectionTitle}>Astuces:</Text>
              {tips.map((tip) => (
                <View key={tip} style={styles.tipItem}>
                  <Ionicons
                    name="bulb-outline"
                    size={16}
                    color={primaryColor}
                    style={styles.tipIcon}
                  />
                  <Text style={styles.tipText}>{tip}</Text>
                </View>
              ))}
            </View>
          )}
        </View>
      )}
    </Animated.View>
  );
};

// PropTypes pour la validation des props
InstructionBox.propTypes = {
  title: PropTypes.string,
  instructions: PropTypes.string,
  examples: PropTypes.arrayOf(PropTypes.string),
  tips: PropTypes.arrayOf(PropTypes.string),
  initiallyExpanded: PropTypes.bool,
  variant: PropTypes.oneOf(['standard', 'compact', 'highlighted']),
  primaryColor: PropTypes.string,
};

// Valeurs par défaut
InstructionBox.defaultProps = {
  title: "Instructions",
  instructions: null,
  examples: [],
  tips: [],
  initiallyExpanded: true,
  variant: "standard",
  primaryColor: "#5E60CE",
};

export default InstructionBox;